         //category submit

       function validateCats(cats) {
        var j = 0;
        var count = 0;
        if(cats.length == 0){
            //vm.errorToast("Enter atleast one category name and code");
            MessageInfo.showMessage(1016, 'Category Name', 'Code', '');
            return false;
        }
        for (var i = 0; i < cats.length; i++) {
          if((cats[i].code =="" && cats[i].name !="") || (cats[i].code !="" && cats[i].name =="")) {
            //vm.errorToast("Enter both code and category name");
            MessageInfo.showMessage(1015, 'Category name', 'code', '');
          }
        }
        for(var i = 0; i < cats.length; i++) {
          var cate = cats[i];
          cate.valid = false;
        }
        for (var i=0; i<cats.length; i++) {
          var arrA = cats[i];
          for (var j=0; j<cats.length; j++) {
            if (i == j){
             // continue;
           } else {
               var arrB = cats[j];
             if (arrA.code == arrB.code) {
               arrA.valid = true;
               arrB.valid = true;
               count++;
               //console.log(count);
             }
             else if (arrA.name == arrB.name) {
               arrA.valid = true;
               arrB.valid = true;
               count++;
             }

           }
           //console.log(arrB);


          }
        }
        if(count >=1) {
          //hols.valid = false;
          console.log(count);
          return false;
        }
        return true;
      }

